@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.NavMain {
  background-color: #fbd05d;
  color: rgb(57, 57, 57);
  display: flex;
  align-items: center;
  padding: 20px;
  height: 70px;
}

.NavMain .nav-list {
  margin-left: auto;
  display: flex;
}

.NavMain .nav-list li:not(:last-child) {
  margin-right: 20px;
}

.header {
  background-color: #fbd05d;
  padding: 16px;
}

.header__title {
  color: #fafafa;
  font-family: "Nanum Pen Script", cursive;
  /* font-family: "Roboto", sans-serif; */
  font-size: 112px;
  text-align: center;
  line-height: 1.2;
  height: 25vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.belly {
  max-width: 1000px;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
}

.belly__item {
  width: calc(50% - 64px);
  margin: 16px;
  padding: 40px 16px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.belly__item {
  background: #a9c12d;
}

.belly__item:nth-of-type(3n + 1),
.belly__item:nth-of-type(4n + 1) {
  background: #ff6e6e;
}

.belly__item__img {
  width: 152px;
  height: auto;
}

.belly__item__text {
  color: #fafafa;
  margin: 0 4px;
}

.belly__item__tile {
  font-family: "Roboto", sans-serif;
  font-size: 24px;
  font-weight: 900;
  text-transform: uppercase;
  margin: 0;
  /* animation: pulse 3s ease-out; */
  /* animation: "pulse" 1s infinite linear; */
  opacity: 0.5;
}

.belly__item__subtitle {
  font-family: "Nanum Pen Script", cursive;
  font-weight: 100;
  font-size: 24px;
  margin: 8px 0;
}

.belly__item__p {
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  font-weight: 300;
  margin: 0;
}

@media screen and (max-width: 750px) {
  .header__nav__linkCont {
    display: none;
  }
  .header__nav__logo {
    opacity: 1;
  }
  .belly {
    flex-direction: column;
  }
  .belly__item {
    width: calc(100% - 64px);
  }
  .belly__item:nth-of-type(odd) {
    background: #ff6e6e;
  }
  .belly__item:nth-of-type(even) {
    background: #a9c12d;
  }
}
@media screen and (max-width: 450px) {
  .belly__item {
    flex-direction: column;
  }
  .belly__item__img {
    width: 200px;
    margin: 0 0 32px;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: #fff7ec;
  font-family: "Nunito Sans", sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  letter-spacing: 0.1px;
  -webkit-font-smoothing: antialiased;
}

.signup-form {
  display: flex;
  flex-direction: column;
  width: 866px;
  padding: 30px 60px;
  background-color: #fff;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
}

.form-header {
  display: flex;
  align-items: center;
  margin-bottom: 50px;
}

.title {
  margin-left: 10px;
  font-size: 25px;
  font-weight: 900;
}

.sign-in {
  margin-left: auto;
  color: #767272;
  font-size: 14px;
}

.title-form {
  font-size: 54px;
  font-weight: 900;
  color: #ebae93;
}

.paragraphe-form {
  font-size: 25px;
  color: #7d4830;
}

.form {
  display: flex;
  flex-wrap: wrap;
  margin-top: 55px;
}

.form-group {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.form-group-item {
  display: flex;
  flex-direction: column;
  width: 50%;
  padding-left: 10px;
  padding-right: 10px;
  margin-bottom: 25px;
}

label {
  font-size: 14px;
  font-weight: bold;
  color: #767272;
  margin-bottom: 7px;
}

input {
  height: 50px;
  padding-left: 20px;
  padding-right: 20px;
  border: 2px solid #767272;
  border-radius: 8px;
  outline: 0;
  transition: ease-out 200ms;
}

.btn-form {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  background-color: #ebae93;
  border: 0;
  padding: 0 50px;
  font-size: 14px;
  color: #fff;
  font-weight: bold;
  border-radius: 100px;
  letter-spacing: 1px;
  cursor: pointer;
  transition: ease-out 200ms;
}

select {
  height: 50px;
  padding-left: 20px;
  padding-right: 20px;
  border: 2px solid #767272;
  border-radius: 8px;
  outline: 0;
  transition: ease-out 200ms;
}

textarea {
  height: 50px;
  padding-left: 20px;
  padding-right: 20px;
  border: 2px solid #767272;
  border-radius: 8px;
  outline: 0;
  transition: ease-out 200ms;
}

button {
  position: relative;
  display: inline-block;
  cursor: pointer;
  outline: none;
  border: 0;
  vertical-align: middle;
  text-decoration: none;
  font-size: inherit;
  font-family: inherit;
}

.learn-more {
  font-weight: 600;
  color: #382b22;
  text-transform: uppercase;
  padding: 1.25em 2em;
  background: #fff0f0;
  border: 2px solid #b18597;
  border-radius: 0.75em;
  transform-style: preserve-3d;
  transition: transform 150ms cubic-bezier(0, 0, 0.58, 1),
    background 150ms cubic-bezier(0, 0, 0.58, 1);
}

#grid-container {
  display: grid;
  grid-template-columns: 30% 30% 30%;
  grid-gap: 1%;
  justify-content: center;
  align-items: center;
  padding: 20px;
  margin: 5%;
}

.grid-item {
  background-image: linear-gradient(to top left, #ff6e6e, #ffa1a1);
  font-family: "Lobster", cursive;
  text-align: center;
  font-size: 1em;
  background-color: rgb(191, 74, 212);
  color: rgb(250, 250, 250);
  padding: 5px;
}

.video-wrapper {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
}

.video-wrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.main-title {
  width: 30%;
  height: auto;
  text-align: center;
  font-family: "Lobster", cursive;
  padding: 10px;
  margin: 30px auto;
  background-image: linear-gradient(to top left, #ff6e6e, #ffa1a1);
  color: rgb(250, 250, 250);
  font-weight: bold;
  font-size: 1em;
  border-radius: 20px;
}

.create-cat {
  text-align: center;
  margin-bottom: 30px;
}


#about-body {
  background: hsla(45, 74%, 82%, 1);
  color: rgb(57, 57, 57);
  font-family: "Lobster", cursive;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px;
}

.about-img {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.children-duo {
  width: 300px;
  height: 300px;
}

.text-about {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 400px;
  height: 400px;
}

.paragraphe-about {
  margin-top: 15px;
}

